import React, {useState} from 'react';
import * as S  from "./Modal.styled";

const Modal = ({ closeModal, addComments, comments, movieId }) => {
    const [comment, setComment] = useState('');
    const [name, setName] = useState('');
    const onChangeName = (e) => setName(e.target.value);
    const onChangeComment = (e) => setComment(e.target.value);
    const onClickPush = () => addComments({comment, id: new Date().valueOf(), name, movieId});
    const curComments = comments?.filter(item => item.movieId === movieId);
    return(
        <S.BackGround>
            <S.ModalWrapper>
                <S.CloseIcon><i className="far fa-times-circle" onClick={closeModal} /></S.CloseIcon>
                <S.CommentsWrapper>
                    {!!curComments?.length ? comments?.map(comment => (
                        <S.CommentWrapper key={comment.id}>
                            <S.Name>{comment.name}:</S.Name>
                            <S.Title>{comment.comment}</S.Title>
                        </S.CommentWrapper>
                    )) : (<S.Title>Нет комментариев</S.Title>)}
                <S.InputWrapper>
                    <input type="text" placeholder="Введите имя" onChange={onChangeName} value={name}/>
                    <input type="text" placeholder="Введите комментарий" onChange={onChangeComment} value={comment}/>
                    <i className="far fa-paper-plane" onClick={onClickPush} />
                </S.InputWrapper>
                </S.CommentsWrapper>
            </S.ModalWrapper>
        </S.BackGround>
    );
}

export default Modal;