import React, { useEffect, useState } from 'react';
import * as S  from "./Main.styled";
import { getRequest } from "../../api/request";
import Header from "../Header/Header";
import Movie from "../Movie/Movie";
import { addStorage, getStorage } from "../../utils/localStorage";
import Modal from "../Modal/Modal";
import Pagination from "../Pagination/Pagination";

const Main = () => {
    const [movies, setMovies] = useState([]);
    const [hideMovies, setHideMovies] = useState([]);
    const [activeMovieId, setActiveMovieId] = useState(null);
    const [comments, setComments] = useState([]);
    const [hideMovieView, setHideMovieView] = useState(false);
    const [openModal, setOpenModal] = useState(false);
    const [activePage, setActivePage] = useState(1);

    const addHideMovie = ({id, title, photo}) => {
        const newHideMovie = hideMovies?.some(item => item.id === id) ?
            hideMovies.filter(item => item.id !== id) :
            [...hideMovies, { id, title, photo }];
        setHideMovies(newHideMovie);
        addStorage('hideMove', newHideMovie);
    };

    const addComments = ({comment, id, name, movieId}) => {
        const newComments = [...comments, { id, name, comment, movieId }];
        setComments(newComments);
        addStorage('comments', newComments);

    };
    const setActivePageToggler = (page) => {
        getRequest('https://yts.mx/api/v2/list_movies.json?page='+page)
            .then(data => setMovies(data.movies));
        setActivePage(page)
    };

    useEffect(() => {
        const saveHideMovieList = getStorage('hideMove') || [];
        const saveComments = getStorage('comments') || [];
        getRequest('https://yts.mx/api/v2/list_movies.json')
            .then(data => setMovies(data.movies));
        setHideMovies(saveHideMovieList);
        setComments(saveComments);
    }, []);

    return(
        <S.Wrapper>
            {openModal && <Modal
                closeModal={() => setOpenModal(false)}
                addComments={addComments}
                comments={comments}
                movieId={activeMovieId}
            />}
            <Header setHideMovie={setHideMovieView}/>
            <S.MovieWrapper>
            {!hideMovieView ?
                movies?.map(({id, title, large_cover_image }) => !hideMovies?.some(item => item.id === id) && (
                    <Movie
                        id={id}
                        key={id}
                        title={title}
                        photo={large_cover_image}
                        addHideMovie={addHideMovie}
                        setOpenModal={setOpenModal}
                        setActiveMovieId={setActiveMovieId}
                    />)
                ) : hideMovies?.map(({id, title, photo }) => (<Movie
                    id={id}
                    key={id}
                    title={title}
                    photo={photo}
                    addHideMovie={addHideMovie}
                    setOpenModal={setOpenModal}
                    setActiveMovieId={setActiveMovieId}
                />))
            }
            </S.MovieWrapper>
            {!hideMovieView && (<Pagination pages={[1, 2, 3]} setActivePage={setActivePageToggler} activePage={activePage}/>)}
        </S.Wrapper>
    );
}

export default Main;