import React from 'react';
import * as S  from "./Movie.styled";

const Movie = ({title, photo, addHideMovie, id, setOpenModal, setActiveMovieId}) => {
    const openComments = () => {
        setActiveMovieId(id);
        setOpenModal(true);
    }
    return(
        <S.MovieWrapper>
            <S.IconWrapper>
                <i className="far fa-comment-dots" onClick={openComments} />
                <i className="far fa-eye-slash" onClick={()=> addHideMovie({id, title, photo})}/>
            </S.IconWrapper>
            <S.Image src={photo}  />
            <S.Title>{title}</S.Title>
        </S.MovieWrapper>
    );
}

export default Movie;