import React from 'react';
import * as S  from "./Pagination.styled";

const Pagination = ({ activePage, pages, setActivePage}) => {
    return(
        <S.Wrapper>
            {pages.map(item => (
                <S.Page
                    onClick={() => setActivePage(item)}
                    active={activePage === item}
                >
                    {item}
                </S.Page>
            ))}
        </S.Wrapper>
    );
}

export default Pagination;